// 

import Cascader from './cascader'
import CascaderPanel from './cascader-panel'

const components = [
  Cascader,
  CascaderPanel
]

const install = Vue => {
  if (install.installed) return

  components.forEach(Component => {
    Vue.component(Component.name, Component)
  })
}

//  Vue
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  install,
  Cascader,
  CascaderPanel
}
